@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Nunito:wght@400;600;700&display=swap);




:root {
    --primary-yellow: #FFBC01;
    --text-black: #181818;
    --text-grey: #494949;
    --background-grey: #F7F7F7;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
}


.signup-page {
    padding: 32px 0 32px 32px;
    display: flex;
    align-items: center;
    background-color: var(--background-grey);
}

.signup--background {
    width: 1004px;
    height: 93vh;
    border-radius: 50px 10px;
    background-image: url(/itechart-internship/static/media/signup-bg.cda552bb.jpg);
}

.form-container {
    padding: 72px 56px;
    border-radius: 50px 10px;
    background-color: #FFFFFF;
    z-index: 1;
}

.form--font {
    font-family: 'Nunito', sans-serif;
}

.form-title {
    margin-top: 48px;
    font-family: 'Montserrat', sans-serif;
    font-size: 40px;
    font-weight: bold;
    line-height: 48px;
}

.form-subtitle {
    margin-top: 8px;
    margin-bottom: 40px;
    font-size: 16px;
    font-weight: 600;
    line-height: 22px;
}

.form-link--yellow {
    font-weight: 600;
    color: var(--primary-yellow);
    text-decoration: none;
}

.form-input {
    margin-top: 8px;
    height: 50px;
    width: 488px;
    border: 1px solid #DADADA;
    border-radius: 8px;
}

.form-input--text {
    font-size: 14px;
    line-height: 19px;
    color: var(--text-grey);
}

.form--column {
    display: flex;
    flex-direction: column;
}

.form--row {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.form-input--margin {
    margin-top: 32px;
}

.form-error--text {
    margin-top: 5px;
    color: #DC143C;
}

.form--translate-right {
    transform: translate(185px);
}

.form--translate-left {
    transform: translate(-185px);
}

.form-submit-button {
    width: 488px;
    height: 54px;
    margin-top: 56px;

    border: none;
    border-radius: 10px;
    background-color: var(--primary-yellow);
    color: var(--text-black);
    font-size: 20px;
    font-weight: bold;
    line-height: 30px;

    cursor: pointer;
}

.form-submit-button:disabled {
    background-color: #494949;
}

.logo {
    width: 137px;
    height: 40px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo-image {
    width: 21px;
}

.logo-title {
    font-family: 'Nunito', sans-serif;
    font-size: 29px;
}

.logo-text--yellow {
    color: var(--primary-yellow);
}

.logo-text--black {
    color: var(--text-black);
}
.signin-page {
    padding: 32px 0 32px 32px;
    display: flex;
    align-items: center;
    background-color: var(--background-grey);
}

.signin--background {
    width: 1004px;
    height: 93vh;
    border-radius: 50px 10px;
    background-image: url(/itechart-internship/static/media/sign-in-bg.8da7ab2c.jpg);
}

